

















































import {Component, Watch, Vue} from 'vue-property-decorator'

@Component
export default class GridLogs extends Vue {
    loading: boolean = true
    tableData: any[] = []
    total: number = 0
    currentPage: number = 1
    pageSize: number = 20
    translations: object = {
        'Status': 'Status',
        'Date': 'Date',
        'Reason': 'Reason',
        'Start Date': 'Start Date',
        'End Date': 'End Date',
        'Duration': 'Duration',
        'Output': 'Output',
    }

    @Watch('$route')
    onChangeRoute() {
        this.fetchLogs()
    }

    created() {
        this.fetchLogs()
    }

    mounted() {
        this.fetchLogs()

        if (typeof ((<any>window).Craft.translations.schedule) !== "undefined") {
            this.translations = (<any>window).Craft.translations.schedule
        }
    }

    indexMethod(index: number) {
        return this.tableData[index].sortOrder
    }

    fetchLogs() {
        let vm = this;

        this.loading = true
        this.tableData = []

        this.$http.post((<any>window).Craft.schedule.logs.api.logs, {
            criteria: {
                schedule: vm.$route.params['handle'],
                offset: (vm.currentPage - 1) * vm.pageSize,
                limit: vm.pageSize,
            }
        }).then(response => {
            vm.loading = false
            vm.total = response.data.total
            vm.tableData = response.data.data
        })
    }

    handleSizeChange(val: number) {
        this.pageSize = val
        this.fetchLogs()
    }

    handleCurrentChange(val: number) {
        this.currentPage = val
        this.fetchLogs()
    }
}
